import { LoadingIndicator } from "./LoadingIndicator";
import { ViewStateService } from './ViewStateService';
import DynamicFieldVisibilityTreatment from "../treatments/form/DynamicFieldVisibilityTreatment";
import { CodeJar } from 'codejar';
import EventUtil from './../utils/EventUtil';
// import { withLineNumbers } from 'codejar/linenumbers';

import Prism from 'prismjs';
import 'prismjs/themes/prism.css'; 
import 'prismjs/components/prism-json';
import InitUIManagers from "../InitUIManagers";

// Import Flatpickr
import flatpickr from 'flatpickr';
// Import the French locale
import { French } from 'flatpickr/dist/l10n/fr.js';
import { DataCalculTreatment } from "../treatments/form/DataCalculTreatment";
import { NotificationHandler } from "./NotificationHandler";


export class FormUI  {
    /**
     * Constructeur de la classe FormUI.
     * @param {string} formSelector - Slecteur CSS du formulaire  grer.
     * @param {ModalUI} modalUI - Instance de ModalUI pour grer les interactions modales.
     */
    constructor(config, indexUI) {
        this.config = config;
        this.indexUI = indexUI;
        this.formSelector = this.config.formSelector
        this.viewStateService = this.config.viewStateService;
        this.loader = new LoadingIndicator(this.formSelector);
        this.dynamicCalculationTreatment = new DataCalculTreatment(config,this);
    }

    /**
     * Initialise le gestionnaire de formulaire.
     */
    init(submitHandler) {


        InitUIManagers.init()

        this.handleCancelButton();
        this.handleCardFooter();
        this.handleFormSubmission(submitHandler);
        this.loader.init();
        this.adapterPourContext();
        this.initializeSelect2_in_modal();
        FormUI.initializeRichText();
        FormUI.initializeDate();
        FormUI.initCodeJar();
        FormUI.initTooltip();
        // Initialisation de la gestion des calculs dynamiques
        this.dynamicCalculationTreatment.init();

        if(window.dynamicFieldVisibilityTreatments){
            new DynamicFieldVisibilityTreatment(window.dynamicFieldVisibilityTreatments)
            .initialize();
        }

    }

    /**
         * Masque les lments <select> dont l'id correspond  une cl dans le contextState.
         */
    adapterPourContext() {

        const scopeData = this.config.viewStateService.getScopeFormVariables();
        const formData = this.config.viewStateService.getFormVariables();

        Object.keys(scopeData).forEach((key) => {
            const filterElement = document.querySelector(`${this.config.formSelector} #${key}`);
            if (filterElement) {
                if (this.config.isDebug) {
                    filterElement.parentElement.style.backgroundColor = 'lightblue'; // Mode debug : surligner
                } else {
                    filterElement.parentElement.style.display = 'none'; // Masquer l'lment du filtre
                }
            }
        });
       
        // Appliquer les valeurs des filtres et masquer si ncessaire
        Object.keys(formData).forEach((key) => {
            const filterElement = document.querySelector(`${this.config.formSelector} #${key}`);
            if (filterElement) {
                    if (filterElement.tagName === "INPUT" || filterElement.tagName === "TEXTAREA") {
                        filterElement.value = formData[key];
                    } else if (filterElement.tagName === "SELECT") {
                        filterElement.value = formData[key];
                        filterElement.dispatchEvent(new Event("change"));
                    }
            }
        });
    }






    /**
     * Gre le bouton d'annulation pour fermer le modal.
     */
    handleCancelButton() {
        EventUtil.bindEvent('click', `${this.formSelector} .form-cancel-button`, (e) => {
            e.preventDefault();
            this.indexUI.modalUI.close();
        });
    }

    /**
     * Modifie le style des pieds de formulaire (footers).
     */
    handleCardFooter() {
        $(`${this.formSelector} .card-footer`).each(function () {
            $(this).removeClass('card-footer').addClass('modal-footer');
        });

        $(`${this.formSelector} .card-body`).each(function () {
            $(this).removeClass('card-body');
        });
    }
   /**
     * Attache un gestionnaire d'vnements pour la soumission du formulaire.
     * @param {Function} submitHandler - Fonction personnalise pour grer la soumission.
     */
   handleFormSubmission(submitHandler) {
    $(document).off('submit', this.formSelector); // Supprime tout gestionnaire prcdent pour viter les doublons
    EventUtil.bindEvent('submit', this.formSelector, (e) => {
        e.preventDefault(); // Empche le rechargement de la page
        submitHandler(); // Appelle la fonction de soumission passe
    });
}

    /**
     * Configure le formulaire pour le mode lecture seule.
     */
    setToReadOnly() {
        const form = $(this.formSelector);
        form.find('input, select, textarea, button').each(function () {
            const element = $(this);
            if (element.is('input') || element.is('textarea')) {
                element.attr('readonly', true);
            } else if (element.is('select')) {
                element.attr('disabled', true);
            } else if (element.is('button')) {
                element.attr('disabled', true);
            }
        });

        form.find('.btn').not('.form-cancel-button').addClass('d-none');
    }

    /**
     * Rinitialise le formulaire  son tat par dfaut.
     */
    resetForm() {
        const form = $(this.formSelector);
        form.trigger('reset');
        form.find('input, select, textarea, button').each(function () {
            $(this).removeAttr('readonly').removeAttr('disabled');
        });

        form.find('.btn').removeClass('d-none');
    }

    /**
     * Srialise et valide les donnes du formulaire avant l'envoi.
     * @returns {Object|null} - Donnes srialises ou null si la validation choue.
     */
    getFormData() {
        const form = $(this.formSelector);
        const data = form.serializeArray();
        const isValid = this.validateForm(data);
        return isValid ? data : null;
    }


    /**
     * Valide les donnes du formulaire.
     * @param {Array} formData - Donnes du formulaire srialises.
     * @returns {boolean} - Retourne `true` si les donnes sont valides, sinon `false`.
     */

    validateForm() {
        const form = $(this.formSelector);
        let isValid = true;
    
        // Remove previous error messages
        form.find('.error-message').remove();

        form.find('[required]').each(function () {
            const field = $(this);
            const value = field.val();
    
            if (field.is(':checkbox')) {
                // Valider une case  cocher
                if (!field.is(':checked')) {
                    field.addClass('is-invalid');

                    // Add an error message if not already added
                    if (!field.next('.error-message').length) {
                        field.after('<span class="error-message text-danger">This field is required.</span>');
                    }

                    isValid = false;
                } else {
                    field.removeClass('is-invalid');
                }
            } else if (field.is('select[multiple]')) {
                // Valider une liste droulante multiple
                if (!value || value.length === 0) {
                    field.addClass('is-invalid');
                    // Ajouter un message d'erreur sous le champ
                    field.after('<span class="error-message text-danger">Veuillez slectionner au moins un lment.</span>');
                    isValid = false;
                } else {
                    field.removeClass('is-invalid');
                }
            } 
            

            else if (field.is('select')) {
                // Validate a single select dropdown
                if (!value || value === 'default') { // Assuming 'default' is your placeholder value
                    field.addClass('is-invalid');
                    if (!field.next('.error-message').length) {
                        field.after('<span class="error-message text-danger">Veuillez slectionner une option.</span>');
                    }
                    isValid = false;
                } else {
                    field.removeClass('is-invalid');
                }

            }







            else if (field.hasClass('richText')) {
                // Validate rich text areas (e.g., Summernote)
                const richTextContent = field.val().trim(); // Get Summernote content
                const richTextContainer = field.next('.note-editor'); // Get Summernote container
    
                if (!richTextContent || richTextContent === '<br>') {
                    richTextContainer.addClass('richText_is_invalid');
    
                    // Add an error message if not already added
                    if (!richTextContainer.next('.error-message').length) {
                        richTextContainer.after('<span class="error-message text-danger">This field cannot be empty.</span>');
                    }
    
                    isValid = false;
                } else {
                    richTextContainer.removeClass('richText_is_invalid');
                    richTextContainer.next('.error-message').remove();
                }
            } 
            
            
            else {
                // Valider les autres champs (texte, email, etc.)
                if (typeof value !== 'string' || !value.trim()) {
                    field.addClass('is-invalid');
                    // Add an error message if not already added
                    if (!field.next('.error-message').length) {
                        field.after('<span class="error-message text-danger">This field cannot be empty.</span>');
                    }
                    isValid = false;
                } else {
                    field.removeClass('is-invalid');
                }
            }
        });
    
        return isValid;
    }



    initializeSelect2_in_modal() {
        

        // Bug : Select2 ne peut pas initialiser deux select avec mme id
        // l'autre select exist dans filter
        // Solution : changement de id de filter
        $(`${this.formSelector} select.select2`).each(function () {
            $(this).select2({
                placeholder: "Slectionnez une option",
                allowClear: true,
            });
        });


     
        // Initialise les lments Select2 avec thme Bootstrap 4
        // $(`.select2bs4`).select2({
        //     theme: 'bootstrap4',
        // });
    }

    static initializeSelect2() {
        // Initialise les lments Select2
        $(`.select2`).select2({
            // placeholder: "Slectionnez une option", // Placeholder requis
            width: '100%',
            // allowClear : true,
        });

        // Initialise les lments Select2 avec thme Bootstrap 4
        $(`.select2bs4`).select2({
            theme: 'bootstrap4',
        });
    }
    static initializeRichText(){


    // Initialiser Summernote
    // $(`.richText`).summernote({
    //     height: 80, // Hauteur de la zone ditable
    // }).on('summernote.change', function() {
    //     // Dclencher l'vnement `change` sur le textarea cach
    //     $(this).trigger('change');
    // });


        // Init sumernote
        $(`.richText`).summernote({
            height: 80, //set editable area's height
        });

        // Utiliser EventUtil pour grer l'vnement `summernote.change`
        EventUtil.bindEvent('summernote.change', '.richText', function() {
            $(this).trigger('change'); // Dclenche le `change` sur le textarea cach
        });


    }

    getFormDataArray() {
        const form = $(this.formSelector);
    
        // Srialiser les donnes en tableau
        const dataArray = form.serializeArray();
    
        return dataArray;
    }

    static initializeDate(){
         // Apply Flatpickr to any element with the class 'datetimepicker'
        flatpickr('.datetimepicker', {
            dateFormat: 'Y-m-d', // Format: 2023-01-01 14:30
            locale: 'fr', 
            allowInput: true, 
            weekNumbers: true,
        });
    }

    static initCodeJar() {
        // Slectionner tous les diteurs avec la classe 'code-editor'
        const editors = document.querySelectorAll('.code-editor');

        editors.forEach((editor) => {
            // Trouver l'input cach associ  cet diteur
            const hiddenInput = editor.nextElementSibling;



            // Fonction de coloration syntaxique
            const highlight = (editorItem) => {
                try {
                    const formattedJSON = JSON.stringify(JSON.parse(editorItem.textContent), null, 2);
                    editorItem.innerHTML = Prism.highlight(formattedJSON, Prism.languages.json, 'json');
                } catch (error) {
                    editorItem.innerHTML = Prism.highlight(editorItem.textContent, Prism.languages.json, 'json');
                }
            };

            // Initialiser CodeJar pour cet diteur
            const jar = CodeJar(editor, highlight);

            // Synchroniser avec l'input cach lors de chaque mise  jour
            jar.onUpdate((editorData) => {
                try {
                    const formattedJSON = JSON.stringify(JSON.parse(editor.textContent), null, 2);
                    editor.style.borderColor = ''; // Bordure normale si valide
                    hiddenInput.value = formattedJSON; // Mettre  jour l'input cach
                } catch (error) {
                    editor.style.borderColor = 'red'; // Bordure rouge si invalide
                }
            });

           // Ajouter un couteur d'vnement sur le hiddenInput
            hiddenInput.addEventListener('change', () => {
                try {
                    editor.textContent = JSON.stringify(JSON.parse(hiddenInput.value), null, 2);
                    highlight(editor);
                } catch (error) {
                    console.error("Erreur lors de la mise  jour de l'diteur :", error);
                }
            });

            // Utiliser EventUtil.bindEvent pour viter les doublons d'vnements
            // TODO : ajouter selector de crud form
            EventUtil.bindEvent('change', `#${hiddenInput.id}`, () => {
                try {
                    editor.textContent = hiddenInput.value;
                    highlight(editor);
                } catch (error) {
                    NotificationHandler.showError("Mettre  jour JSON : " + error);
                    console.error("Erreur lors de la mise  jour de l'diteur :", error);
                }
            });


            // Charger le contenu initial, s'il existe
            if (hiddenInput.value) {
                editor.textContent = JSON.stringify(JSON.parse(hiddenInput.value), null, 2);
                highlight(editor);
            }
        });
    }


    /**
     * Affiche les erreurs sous les champs concerns
     * @param {Object} errors - Erreurs renvoyes par le serveur (format Laravel)
     */
    showFieldErrors(errors) {
        const form = $(this.formSelector);
        form.find('.error-message').remove(); // Supprime les anciens messages d'erreur

        Object.entries(errors).forEach(([field, messages]) => {
            let input = form.find(`[name="${field}"]`);
            if (input.length > 0) {
                input.addClass('is-invalid');
                messages.forEach(msg => {
                    input.after(`<div class="error-message text-danger">${msg}</div>`);
                });
            }
        });
    }


    static initTooltip(){
        $('[data-toggle="tooltip"]').tooltip();
    }
  

}
