

import { AjaxErrorHandler } from "../../components/AjaxErrorHandler";
import { LoadingIndicator } from '../../components/LoadingIndicator';
import EventUtil from '../../utils/EventUtil';

export class DataCalculTreatment {
    /**
     * Constructeur pour initialiser la gestion des calculs dynamiques.
     * @param {string} formSelector - Slecteur du formulaire  grer.
     * @param {string} calculationUrl - URL de lAPI pour recalculer les valeurs dpendantes.
     */
    constructor(config, formUI) {
        this.formUI = formUI;
        this.config = config;
        this.debounceDelay = 500; // Dlai pour limiter les appels AJAX
        this.currentRequest = null; // Stocke la requte en cours
        this.lastSentData = null; // Stocke les dernires donnes envoyes
        this.loader = new LoadingIndicator(this.config.formSelector);
    }

    /**
     * Initialise la gestion des vnements pour dtecter les modifications des champs dynamiques.
     */
    init() {
        // Gestion des champs texte, nombre, textarea
        EventUtil.bindEvent('input', `${this.config.formSelector} [data-calcul="true"]:not(select, [type="checkbox"], [type="radio"])`, (e) => {
            this.debouncedCalculate(e.currentTarget);
        });
    
        // Gestion des <select> et des cases  cocher avec "change"
        EventUtil.bindEvent('change', `${this.config.formSelector} [data-calcul="true"]`, (e) => {
            this.debouncedCalculate(e.currentTarget);
        });
    }

    /**
     * Applique un dlai avant d'envoyer la requte AJAX pour viter les requtes excessives.
     * @param {HTMLElement} field - Champ modifi.
     */
    debouncedCalculate(field) {
        clearTimeout(this.debounceTimeout);
        this.debounceTimeout = setTimeout(() => {
            this.triggerCalculation(field);
        }, this.debounceDelay);
    }

    /**
     * Envoie une requte AJAX au serveur pour recalculer les valeurs dpendantes.
     * @param {HTMLElement} field - Champ modifi.
     */
    triggerCalculation(sourceField) {
        const form = $(this.config.formSelector);

         // Supprimer la mthode put en cas de update
        let formData = form.serializeArray();
        formData = formData.filter(field => field.name !== "_method");
        formData = $.param(formData);


        // Vrifier si les donnes envoyes sont identiques  la dernire requte pour viter les doublons
        if (this.lastSentData === formData) return;
        this.lastSentData = formData;

        // Annuler la requte prcdente si elle est en cours
        if (this.currentRequest !== null) {
             this.currentRequest.abort();
        }

        // Il dirrange l'utilisateur
        this.loader.showNomBloquante();
        this.currentRequest = $.ajax({
            url: this.config.calculationUrl,
            method: 'POST',
            data: formData,
            dataType: 'json',
        })
            .done((response) => {
                // sourceField.prop('disabled', false);
                this.updateDependentFields(sourceField,response.entity);
            })
            .fail((xhr, textStatus) => {
                // Ignorer les erreurs d'annulation de requte AJAX
                if (textStatus === 'abort') {
                    return;
                }
                if (xhr.responseJSON?.errors) {
                    this.formUI.showFieldErrors(xhr.responseJSON.errors);
                } else {
                   
                    AjaxErrorHandler.handleError(xhr, "Erreur lors du recalcul.");
                }
            })
            .always(() => {
                this.loader.hide();
                this.currentRequest = null;
            });
    }

    /**
     * Met  jour les champs dpendants avec les nouvelles valeurs reues du serveur.
     * @param {Object} response - Donnes mises  jour reues du serveur.
     */
    updateDependentFields(sourceField, response) {
        Object.entries(response).forEach(([key, value]) => {
         
             // Slectionner le champ en tenant compte des crochets `[]` pour `select multiple`
            const field = $(`${this.config.formSelector} [name="${key}"], ${this.config.formSelector} [name="${key}[]"]`);

           // Vrifier si c'est le champ qui a dclench l'vnement, viter de le mettre  jour
        if (field.is(sourceField)) {
            return;
        }

            if (field.length > 0) {

                // Vrifier si c'est un select multiple
                if (field.is('select[multiple]')) {
                    let values = [];

                    if (Array.isArray(value)) {
                        // Extraire uniquement les IDs si les valeurs sont des objets
                        values = value.map(v => (typeof v === 'object' && v !== null ? v.id : v));
                    }
                    field.val(values).trigger('change');
                }

                // Vrifier si c'est un select2
                else if (!field.is('select[multiple]') && field.is('select')) {
                    field.val(value).trigger('change'); // Mettre  jour et dclencher l'vnement
                } 
                // Vrifier si c'est un champ de type checkbox ou radio
                else if (field.is(':checkbox, :radio')) {
                    field.prop('checked', !!value);
                } 
                // Vrifier si c'est un champ input classique ou textarea
                else {
                    field.val(value).trigger('change');
                }
            }
        });
    }
    
}
